            </div>           
    </div>


			<div class="form-content" v-if="step==2" >
				<p class="form-title">请输入6位数验证码</p>
				<div class="input-box">
					<div class="pwdbox" @click="getFocus" >
						<input class="pd-input num1"  readonly v-model="num1" type="number" maxlength="1" />
						<input class="pd-input num2"  readonly v-model="num2" type="number" maxlength="1" />
						<input class="pd-input num3"  readonly v-model="num3" type="number" maxlength="1" />
						<input class="pd-input num4"  readonly v-model="num4" type="number" maxlength="1" />
						<input class="pd-input num5"  readonly v-model="num5" type="number" maxlength="1" />
						<input class="pd-input num6"  readonly v-model="num6" type="number" maxlength="1" />
					</div>
