                                placeholder="密码"
                                :rules="[{ required: true, message: '请填写密码' }]"
                            />
                             <van-field
                                v-model="form.password1"
                                label="再次确认"
                                placeholder="密码"
                                :rules="[{ required: true, message: '请填写密码' }]"
                            />                           
