<script>
import {getAuthCode,getCountryCode} from './service' 
import Vue from 'vue';
import { Popup,Icon,Form,Field,Button,Toast,IndexAnchor,IndexBar,Cell } from 'vant';
Vue.use(IndexBar);
Vue.use(Cell);
Vue.use(IndexAnchor);
Vue.use(Toast);
Vue.use(Button);
Vue.use(Icon);
Vue.use(Popup);
Vue.use(Form);
Vue.use(Field);
export default {
components: {},
data() {
    return {
        loading:false,
        showCountry:false,
        country:{},       
        step:1,
        showPopup:false,
        form:{
            phone:'',
            password:'',
            password1:'',
            country_code:'zh-cn',
            form_id:'form-130'
        },
        num1:'',
        num2:'',
        num3:'',
        num4:'',
        num5:'',
        num6:'',
        payFocus:false,
        pwdInputval:'',
        countryList:[
            {index:'A',list:[]},
            {index:'B',list:[]},
            {index:'C',list:[]},
            {index:'D',list:[]},
            {index:'E',list:[]},
            {index:'F',list:[]},
            {index:'G',list:[]},
            {index:'H',list:[]},
            {index:'I',list:[]},
            {index:'J',list:[]},
            {index:'K',list:[]},
            {index:'L',list:[]},
            {index:'M',list:[]},
            {index:'N',list:[]},
            {index:'O',list:[]},
            {index:'P',list:[]},
            {index:'Q',list:[]},
            {index:'S',list:[]},
            {index:'T',list:[]},
            {index:'U',list:[]},
            {index:'V',list:[]},
            {index:'W',list:[]},
            {index:'X',list:[]},
            {index:'Y',list:[]},
            {index:'Z',list:[]},
        ]       
    };
},
computed: {},
watch: {},
methods: {
    selCountry(row){
        this.country = row;
        this.showCountry = false;
    },
    getCountryCode(){
        getCountryCode().then(res=>{
              let vm = this;
              res.data.forEach(item=>{
                  if(item.iso3==res.meta.default.iso3){
                      vm.country = item;
                  }
              }) 
              this.countryList.forEach(item=>{
                res.data.forEach(subItem=>{
                    if(item.index==subItem.label.substr(0, 1)){
                        item.list.push(subItem);
                    }
                })                  
              })  
        }).catch(res=>{
            Toast(res.message);
        })
    },    
    toLogin(){
        this.$router.push({path:'/login'})
    },
    onSubmit(){
        if(!this.form.phone){
            Toast('请输入电话号码');
            return false;
        }
        let data = Object.assign({},this.form);
        data.phone = '+'+this.country.calling_code+'-'+ data.phone;
        data.country_code = this.country.iso3;
        getAuthCode(data).then(res=>{
            console.log(res);
            Toast.success('验证码已发送，请注意查收'); 
            this.step = 2;         
        }).catch(res=>{
            Toast(res.message);
        })
    },
    inputChange(){
				let arr = this.pwdInputval.split('');
				if(arr.length != 6) {
					for(let i = 0; i < 6 - arr.length; i++) {
						arr.push('');
					}
				}
				this.num1 = arr[0];
				this.num2 = arr[1];
				this.num3 = arr[2];
				this.num4 = arr[3];
				this.num5 = arr[4];
				this.num6 = arr[5];
    },
    getFocus(){
        this.$refs.codeInput.focus();
    }
},
created() {

},
mounted() {
    this.getCountryCode();
},
beforeCreate() {},
beforeMount() {},
beforeUpdate() {},
updated() {},
beforeDestroy() {},
destroyed() {},
activated() {},
}
</script>
